<template>
  <div class="container">
    <div class="limit-width center scroll-x">
      <!-- upload-input -->
      <input
        type="file"
        ref="file"
        style="visibility: hidden; width:0; height: 0"
        @change="onFileChange"
        required="application/msexcel"
        accept=".csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      >
      <!-- upload-input:end -->
      <!-- btns -->
      <div class="btns">
        <el-button
          :type="list && list.length ? 'danger' : 'success' "
          @click="uploadTable"
        >{{list && list.length ? '' : ''}}</el-button>
        <el-button type="primary" @click="translate" :disabled="!list || !list.length"></el-button>
        <el-button type="primary" :disabled="!canWriteIn" @click="writeIn"></el-button>
      </div>
      <!-- btns:end -->
      <!-- table -->
      <div ref="table">
        <el-table v-loading="loading" :data="list" border fit show-header>
          <el-table-column
            v-for="collumn in collumns"
            :key="collumn.prop"
            :prop="collumn.prop"
            :label="collumn.label"
            :width="collumn.width"
          ></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
              <el-popover placement="top" width="280">
                <p>"{{scope.row.name}}({{scope.row.goods_id}})"</p>
                <div style="text-align: right; margin: 0">
                  <el-button size="mini" type="text" @click="$refs.table.click()"></el-button>
                  <el-button type="primary" size="mini" @click="handleDelete(scope.$index)"></el-button>
                </div>
                <el-button slot="reference" size="mini" type="danger"></el-button>
              </el-popover>
              <!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button> -->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- table:end -->
      <!-- dialogs -->
      <edit-form @on-update-item="onUpdateItem" ref="editForm"></edit-form>
      <!-- dialogs:end -->
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import axios from "axios";
import axiosjsonp from "axios-jsonp";
import _ from "lodash";

import editForm from "./editForm";

export default {
  components: {
    "edit-form": editForm
  },
  data() {
    return {
      list: null,
      workBook: null,
      hasTranslated: false,
      loading: false,
      collumns: [
        {
          prop: "goods_id",
          label: "",
          width: "160"
        },
        {
          prop: "name",
          label: "",
          width: "200"
        },
        {
          prop: "name_e",
          label: "()",
          width: "200"
        },
        {
          prop: "addr",
          label: "",
          width: "300"
        },
        {
          prop: "addr_e",
          label: "()",
          width: "300"
        }
      ]
    };
  },
  computed: {
    canWriteIn() {
      return this.list && this.list.length && this.hasTranslated;
    }
  },
  watch: {},
  methods: {
    // 
    uploadTable() {
      this.hasTranslated = false;
      if (this.list && this.list.length) {
        return this.delTable();
      }
      // 
      this.$refs.file.value = "";
      this.$refs.file.click();
    },
    // 
    onFileChange() {
      this.readExcel();
    },
    // Excel -> workbook
    readExcel() {
      let self = this;
      let file = this.$refs.file.files[0];
      if (!file) return;
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = function(e) {
        var workBook = XLSX.read(e.target.result, { type: "buffer" });
        if (!workBook.Workbook) {
          return;
        }
        self.workBook = workBook;

        let firstSheetName = workBook.SheetNames[0];
        let firstSheet = workBook.Sheets[firstSheetName];
        let goodsData = XLSX.utils.sheet_to_json(firstSheet, {
          header: "A",
          blankrows: false,
          defval: ""
        });
        goodsData = goodsData.map(e => ({
          goods_id: String(e.A),
          name: String(e.B),
          name_e: "",
          addr: String(e.C),
          addr_e: ""
        }));
        self.list = goodsData;
        // var excelData = XLSX.utils.sheet_to_json(workBook.Sheets.Sheet1, { header: "A", blankrows: false, defval: ""});
        // console.log("excelData: ",excelData);
      };
    },
    delTable() {
      this.list = null;
    },
    async translate() {
      this.hasTranslated = false;
      let chunckList = _.chunk(this.list, 100);
      let result = [];
      this.loading = true;
      for (let i = 0, len = chunckList.length; i < len; i++) {
        let chunck = chunckList[i];
        try {
          let chunckRst = await this.commTask(chunck);
          result = result.concat(chunckRst);
          console.log(chunckRst);
        } catch (err) {
          let errResp = err.response;

          if (errResp && errResp.status === 401) {
            this.$emit("not-login");
          } else {
            this.$message({
              message: "" + err.message,
              type: "error"
            });
          }

          this.loading = false;

          return;
        }
      }
      this.list = result;
      this.hasTranslated = true;
      this.loading = false;
      this.$message({
        message: "",
        type: "success"
      });
    },
    async commTask(chunck) {
      let access = {};
      let q = encodeURIComponent(
        chunck.map(e => `${e.name || "-"}\n${e.addr || "-"}`).join("\n")
      );
      let { resp, err } = await this.fetchBaiduAccess(q);
      if (err) {
        throw err;
      } else {
        access = resp.data;
      }
      try {
        let resp = await this.$http.get(this.$config.baiduFY, {
          params: {
            q: access.q,
            appid: access.appid,
            salt: access.salt,
            from: access.from,
            to: access.to,
            sign: access.sign
          },
          adapter: axiosjsonp
        });
        let trans_result = resp.data.trans_result;
        let list = chunck.slice();
        for (let i in list) {
          list[i].name_e = trans_result[i * 2].dst;
          list[i].addr_e = trans_result[i * 2 + 1].dst;
        }
        return list;
      } catch (err) {
        throw new Error("baiduFY error: ", err.message);
      }
    },
    writeIn() {
      if (!this.canWriteIn) {
        return;
      }
      this.loading = true;
      this.$api
        .post("/goods/list", {
          list: this.list
        })
        .then(res => {
          this.loading = false;
          this.$message({
            message: "",
            type: "success"
          });
        })
        .catch(err => {
          this.loading = false;
          let resp = err.response;
          if (resp && resp.status === 401) {
            this.$emit("not-login");
          }
          this.$message({
            message: "" + resp.response.data.message,
            type: "error"
          });
          return;
        });
    },
    // token
    fetchBaiduAccess(q) {
      return new Promise((resolve, reject) => {
        this.$api
          .get("/baiduFY/access?q=" + q)
          .then(resp => {
            resolve({ resp: resp, err: null });
          })
          .catch(err => {
            resolve({ resp: null, err: err });
          });
      });
    },
    // 
    handleDelete(i) {
      this.list.splice(i, 1);
      this.$message("");
      if (!this.list.length) {
        this.list = null;
        this.hasTranslated = false;
      }
      // dialog
      this.$refs.table.click();
    },
    // 
    handleEdit(row) {
      this.$refs.editForm.open(row);
    },
    // 
    onUpdateItem(data, cb) {
      if (!data) return cb();
      let list = this.list.slice();
      for (let i = 0, len = list.length; i < len; i++) {
        if (list[i].goods_id === data.goods_id) {
          list.splice(i, 1, data);
          break;
        }
      }
      this.list = list;

      cb();
    }
  }
};
</script>

<style lang="stylus" scoped>
.container
  width 100%
  box-sizing border-box
  height 100vh
  background #fefefe
  padding 16px
  .btns
    text-align left
    margin-bottom 16px
</style>
